import React from 'react'
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button'
import Snackbar from '@material-ui/core/Snackbar'
import SnackbarContent from '@material-ui/core/SnackbarContent'
import Typography from '@material-ui/core/Typography'
import EmailIcon from '@material-ui/icons/Email'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
  root: {
    color: theme.palette.primary.contrastText,
    background: theme.palette.primary.main
  },
  checkEmailMsgIco: {
    marginRight: theme.spacing(1)
  },
  checkEmailMsgTxt: {
    textAlign: 'left',
    flexGrow: 1
  },
  checkEmailMsg: {
    display: 'flex'
  }
}))

const CheckemailMsg = ({ onResend, open }) => {
  const classes = useStyles()
  return (
    <Snackbar
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'center'
      }}
      open={open}
    >
      <SnackbarContent
        className={classes.root}
        message={<div className={classes.checkEmailMsg}>
          <EmailIcon className={classes.checkEmailMsgIco} color='inherit' />
          <Typography className={classes.checkEmailMsgTxt} color='inherit'>Code Sent Check your email</Typography>
        </div>}
        action={onResend && [
          <Button key='undo' color='secondary' size='small' variant='contained' onClick={onResend}>
                Resend
          </Button>
        ]}
      />
    </Snackbar>
  )
}

CheckemailMsg.propTypes = {
  onResend: PropTypes.func,
  open: PropTypes.bool
}

export default CheckemailMsg
